@font-face {
  font-family:'myFont1' ;
  src: url('../../fontfamily/ZCOOLXiaoWei-Regular.ttf');
}
a:hover{
  cursor: pointer
}
.ant-pro-form-login-page-title{
  background-image: linear-gradient(to bottom , rgb(255, 156, 14), rgb(240, 123, 13));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family:'myFont1' ;  
}
.ib{
  display: inline;
}
/* footer样式 */
:where(.css-dev-only-do-not-override-18iikkb).ant-layout-footer {
  padding: 5px 50px;
}
/* header样式 */
:where(.css-dev-only-do-not-override-18iikkb).ant-layout-header {
      color: none;
}
.ant-pro-form-login-page-left{
  overflow: unset;
}
.nav-left{
  margin-Left: 2vh;
  float: left;
  font-size: 3vh;
}
.nav-right{
  float: right;
  height: 4.5vh;
 line-height: 4.5vh;
 color:rgb(238, 242, 241);
}
.nav-right a{
color: aliceblue;
}
.nav-right a:hover{
  color: rgb(58, 143, 185);
}
.nav-left-span{
  margin-left: 1vh;
  float: right;
  height: 4.5vh;
  line-height:4.5vh ;
  font-family:'myFont1' ;  
  color: white;
}.myorange:hover{
  color:rgb(255, 156, 14) ;
}
